
body{
  width: 100%;
  height: 100%;
  background: url(data:image/png;base64,) repeat;
  background-size: cover;
  overflow-x: hidden;
  /*background-color: #fefefe;*/
}
#app:before{
    position: absolute;
    display: block;
    width: 100%;
    z-index: -1;
    content: '';
    min-height: 500px;
    -webkit-filter: blur(1px);
            filter: blur(1px);
    background: url(../../static/img/bg.465adb6.jpg) no-repeat;
    background-size: 100% 500px;
    -webkit-animation: bgmove 30s linear infinite;
            animation: bgmove 30s linear infinite;
}
@-webkit-keyframes bgmove {
0%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
50%{
     -webkit-transform: translateX(70px);
             transform: translateX(70px);
}
100%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
}
@keyframes bgmove {
0%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
50%{
     -webkit-transform: translateX(70px);
             transform: translateX(70px);
}
100%{
     -webkit-transform: translateX(-70px);
             transform: translateX(-70px);
}
}
.clear:after {
  display: block;
  content: '';
  clear: both;
}
 /*css抛物线动画*/
.animtion{
  margin-top: 10px;
  width: 100px;
  height: 100px;
  border: solid #ddd;
  border-width: 2px 2px 0 0;
  background: -webkit-gradient(linear, left bottom, left top, color-stop(10%, #ddd), color-stop(10%, transparent)), -webkit-gradient(linear, left top, right top, color-stop(10%, #ddd), color-stop(10%, transparent));
  background: linear-gradient(to top, #ddd 10%, transparent 10%), linear-gradient(to right, #ddd 10%, transparent 10%);
  background-size: 100% 20%, 20%, 100%;
  position: relative;
}
.anitem ,.anitem2{
  width:20px;
  height: 20px;
  top:0;
  left: 0;
  display: inline-block;
  position: absolute;
  background-color: #00aa00;
  border-radius: 50%;
}
.anwraper {
  -webkit-animation: vertical-animation 2s  .5s infinite alternate;
          animation: vertical-animation 2s  .5s infinite alternate;
  -webkit-animation-timing-function: cubic-bezier(.11,-.55,.55,.11);
          animation-timing-function: cubic-bezier(.11,-.55,.55,.11);
}
.anwraper .anitem {
  -webkit-animation: hor-animation 2s linear .5s infinite alternate;
          animation: hor-animation 2s linear .5s infinite alternate;
}
@-webkit-keyframes hor-animation {
0% { -webkit-transform: translateX(0px); transform: translateX(0px);
}
100% { -webkit-transform: translateX(100px); transform: translateX(100px);
}
}
@-webkit-keyframes vertical-animation {
0% { -webkit-transform: translateY(0px); transform: translateY(0px);
}
100% { -webkit-transform: translateY(100px); transform: translateY(100px);
}
}
